"use client";

import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Switch } from "@/components/ui/switch";
import { useState } from "react";
import { useToast } from "@/hooks/use-toast";
import { Save, PlusCircle, Trash2 } from "lucide-react";

interface EnvVar {
  id: string;
  key: string;
  value: string;
}

export default function ConfigurationPage() {
  const { toast } = useToast();
  const [envVars, setEnvVars] = useState<EnvVar[]>([
    { id: "1", key: "API_KEY", value: "********" },
    { id: "2", key: "DATABASE_URL", value: "postgres://...." },
  ]);
  const [newKey, setNewKey] = useState("");
  const [newValue, setNewValue] = useState("");

  const handleSaveSettings = (settingsName: string) => {
    // Mock save operation
    toast({
      title: `${settingsName} Settings Saved (Mock)`,
      description: `Your ${settingsName.toLowerCase()} configurations have been updated.`,
    });
  };

  const addEnvVar = () => {
    if (newKey && newValue) {
      setEnvVars([...envVars, { id: Date.now().toString(), key: newKey, value: newValue }]);
      setNewKey("");
      setNewValue("");
      toast({ title: "Environment Variable Added (Mock)" });
    } else {
      toast({ title: "Error", description: "Key and Value cannot be empty.", variant: "destructive" });
    }
  };

  const removeEnvVar = (id: string) => {
    setEnvVars(envVars.filter(v => v.id !== id));
    toast({ title: "Environment Variable Removed (Mock)" });
  };
  
  const updateEnvVar = (id: string, field: 'key' | 'value', val: string) => {
    setEnvVars(envVars.map(v => v.id === id ? { ...v, [field]: val } : v));
  };


  return (
    <div className="container mx-auto py-8">
      <div className="mb-8">
        <h1 className="text-3xl font-bold">Configuration</h1>
        <p className="text-muted-foreground">Manage settings for Google Genkit, Firebase Hosting, and environment variables.</p>
      </div>

      <Tabs defaultValue="genkit" className="w-full">
        <TabsList className="grid w-full grid-cols-3 mb-6">
          <TabsTrigger value="genkit">Google Genkit</TabsTrigger>
          <TabsTrigger value="firebase">Firebase Hosting</TabsTrigger>
          <TabsTrigger value="env">Environment Variables</TabsTrigger>
        </TabsList>

        <TabsContent value="genkit">
          <Card className="shadow-lg">
            <CardHeader>
              <CardTitle>Google Genkit Configuration</CardTitle>
              <CardDescription>Set up your Google Genkit integration.</CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              <div className="space-y-2">
                <Label htmlFor="genkit-api-key">API Key</Label>
                <Input id="genkit-api-key" type="password" placeholder="Enter your Genkit API Key" defaultValue="gsk_xxxxxxxxxxxxxxxxxxxx" />
              </div>
              <div className="space-y-2">
                <Label htmlFor="genkit-model">Default Model</Label>
                <Input id="genkit-model" placeholder="e.g., gemini-pro" defaultValue="gemini-2.0-flash" />
              </div>
              <div className="flex items-center space-x-2">
                <Switch id="genkit-enable-tracing" defaultChecked />
                <Label htmlFor="genkit-enable-tracing">Enable Tracing</Label>
              </div>
              <Button onClick={() => handleSaveSettings("Genkit")} className="w-full md:w-auto">
                <Save className="mr-2 h-4 w-4" /> Save Genkit Settings
              </Button>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="firebase">
          <Card className="shadow-lg">
            <CardHeader>
              <CardTitle>Firebase Hosting Configuration</CardTitle>
              <CardDescription>Configure your Firebase Hosting deployment settings.</CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              <div className="space-y-2">
                <Label htmlFor="firebase-project-id">Firebase Project ID</Label>
                <Input id="firebase-project-id" placeholder="your-firebase-project-id" defaultValue="agent-computer-app"/>
              </div>
              <div className="space-y-2">
                <Label htmlFor="firebase-site-id">Hosting Site ID</Label>
                <Input id="firebase-site-id" placeholder="your-hosting-site-id (optional)" defaultValue="agent-computer-web"/>
              </div>
              <div className="flex items-center space-x-2">
                <Switch id="firebase-auto-deploy" />
                <Label htmlFor="firebase-auto-deploy">Enable Auto-Deployment on Push to Main</Label>
              </div>
              <Button onClick={() => handleSaveSettings("Firebase")} className="w-full md:w-auto">
                <Save className="mr-2 h-4 w-4" /> Save Firebase Settings
              </Button>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="env">
          <Card className="shadow-lg">
            <CardHeader>
              <CardTitle>Environment Variables</CardTitle>
              <CardDescription>Manage your project's environment variables. These are mock values.</CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              <div className="space-y-4">
                {envVars.map((variable) => (
                  <div key={variable.id} className="flex items-center space-x-2 p-3 border rounded-md bg-muted/30">
                    <Input 
                      placeholder="Key (e.g., API_KEY)" 
                      value={variable.key} 
                      onChange={(e) => updateEnvVar(variable.id, 'key', e.target.value)}
                      className="flex-1"
                    />
                    <Input 
                      type="password" 
                      placeholder="Value" 
                      value={variable.value} 
                      onChange={(e) => updateEnvVar(variable.id, 'value', e.target.value)}
                      className="flex-1"
                    />
                    <Button variant="ghost" size="icon" onClick={() => removeEnvVar(variable.id)} className="text-destructive hover:text-destructive/80">
                      <Trash2 className="h-4 w-4" />
                    </Button>
                  </div>
                ))}
              </div>
              <div className="flex items-end space-x-2 pt-4 border-t">
                <div className="flex-1 space-y-1">
                  <Label htmlFor="new-env-key">New Key</Label>
                  <Input id="new-env-key" placeholder="VARIABLE_NAME" value={newKey} onChange={(e) => setNewKey(e.target.value)} />
                </div>
                <div className="flex-1 space-y-1">
                  <Label htmlFor="new-env-value">New Value</Label>
                  <Input id="new-env-value" placeholder="Value" value={newValue} onChange={(e) => setNewValue(e.target.value)} />
                </div>
                <Button onClick={addEnvVar} variant="outline" className="shrink-0">
                  <PlusCircle className="mr-2 h-4 w-4" /> Add Variable
                </Button>
              </div>
               <Button onClick={() => handleSaveSettings("Environment Variables")} className="w-full md:w-auto">
                <Save className="mr-2 h-4 w-4" /> Save Environment Variables
              </Button>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  );
}
